import classes from "./Header.module.css";
import {Link, useNavigate} from "react-router-dom";
import AuthContext from "../../../store/auth-context";
import {useContext, useEffect, useState} from "react";

export default function HeaderForm() {
    const authCtx = useContext(AuthContext);
    const navigate = useNavigate();
    const [isLogin, setIsLogin] = useState<boolean>(!!localStorage.getItem('authorization'));

    useEffect(() => {
        setIsLogin(authCtx.isLoggedIn)
    }, [authCtx.isLoggedIn])

    const toLoginPage = () => {
        navigate('/auth');
    }

    const loginForm = <button type="button" className="btn btn-outline-light me-2" onClick={toLoginPage}>Login</button>;

    const logoutForm = <button type="button" className="btn btn-outline-light me-2" onClick={authCtx.logout}>Logout</button>;

    return (
        <>
            {/*<header className={classes.header}>*/}
            {/*<header className={classes.header}>*/}
            {/*    <h1><Link to={'/home'}>Web Party</Link></h1>*/}
            {/*    <div><Link to={'/board'} state={{category: 'free'}}>자유게시판</Link></div>*/}
            {/*    <div><Link to={'/board'} state={{category: 'notice'}}>공지사항</Link></div>*/}
            {/*    <div><Link to={'/study'} state={{category: 'free'}}>스터디</Link></div>*/}
            {/*    <div>검색</div>*/}
            {/*    {isLogin ? logoutForm : loginForm}*/}
            {/*    {authCtx.isLoggedIn && <div><Link to={"/my-page"}>{localStorage?.getItem("nickname")}</Link></div>}*/}
            {/*    <div/>*/}
            {/*</header>*/}


            <header className="p-3 text-bg-dark">
                <div className="container">
                    <div
                        className="d-flex flex-wrap align-items-center justify-content-center justify-content-lg-start">
                        <a href="/" className="d-flex align-items-center mb-2 mb-lg-0 text-white text-decoration-none">
                            <svg className="bi me-2" width="40" height="32" role="img" aria-label="Bootstrap">
                                <use xlinkHref="#bootstrap"></use>
                            </svg>
                        </a>

                        <ul className="nav col-12 col-lg-auto me-lg-auto mb-2 justify-content-center mb-md-0">
                            <li><a href="#" className="nav-link px-2 text-secondary">Home</a></li>
                            <li><a href="#" className="nav-link px-2 text-white">Features</a></li>
                            <li><a href="#" className="nav-link px-2 text-white">Pricing</a></li>
                            <li><a href="#" className="nav-link px-2 text-white">FAQs</a></li>
                            <li><a href="#" className="nav-link px-2 text-white">About</a></li>
                        </ul>

                        <form className="col-12 col-lg-auto mb-3 mb-lg-0 me-lg-3" role="search">
                            <input type="search" className="form-control form-control-dark text-bg-dark"
                                   placeholder="Search..."
                                   aria-label="Search"/>
                        </form>

                        <div className="text-end">
                            {isLogin ? logoutForm : loginForm}
                            <button type="button" className="btn btn-warning">Sign-up</button>
                        </div>
                    </div>
                </div>
            </header>
        </>
    )

}

